@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background: url(/drum-machine/static/media/bg.f0919ba1.jpg) no-repeat center center fixed;
  background-size: cover;
}

#wrapper {
  font-family: "Poppins", sans-serif;
}

#wrapper h2,
#wrapper h3 {
  text-transform: uppercase;
  font-weight: normal;
  font-style: italic;
  color: white;
}

#wrapper h3 {
  font-size: 22px;
}

#wrapper .portfolio {
  position: absolute;
  top: -7px;
  left: 0;
  display: flex;
  z-index: 99;
  height: 40px;
  width: 230px;
  color: white;
  font-weight: 400;
  background: rgba(67, 149, 255, 0.6);
  border-radius: 0 0 40px 0;
  -moz-border-radius: 0 0 40px 0;
  -webkit-border-radius: 0 0 40px 0;
  font-style: italic;
  font-size: 14px;
  vertical-align: middle;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  padding-left: 10px;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

#wrapper .portfolio:hover {
  background: rgba(0, 255, 128, 0.6);
  color: white;
}

#wrapper .portfolio:active {
  background: white;
  color: #ff00ea;
}

#wrapper #drumMachine {
  display: flex;
  width: 1000px;
  height: 500px;
  background: rgba(255, 153, 229, 0.6);
  border: 3px solid white;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#wrapper #drumMachine #padWrap {
  width: 400px;
  height: 400px;
  margin: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

#wrapper #drumMachine #padWrap .pad {
  width: 30%;
  height: 30%;
  border: 2px solid white;
  border-radius: 10px;
  margin: auto;
  cursor: pointer;
  font-size: 16px;
  color: #006eff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  line-height: 0;
  background: rgba(255, 255, 255, 0.5);
}

#wrapper #drumMachine #padWrap .pad .letter {
  font-size: 36px;
  text-transform: uppercase;
}

#wrapper #drumMachine #controlWrap {
  width: 400px;
  margin: 50px;
}

#wrapper #drumMachine #controlWrap #title {
  width: 100%;
  margin: 0;
  display: inline-block;
  text-align: center;
  margin-bottom: 10px;
  font-size: 50px;
  font-weight: 300;
  font-style: italic;
  text-shadow: 7px 7px magenta;
}

#wrapper #drumMachine #controlWrap #trackPlayer {
  width: 50%;
  display: float;
  float: left;
}

#wrapper #drumMachine #controlWrap #trackPlayer .track {
  width: 100%;
  display: flex;
  font-size: 18px;
  margin-bottom: 20px;
  cursor: pointer;
}

#wrapper #drumMachine #controlWrap #trackPlayer .track .trackToggle {
  width: 13px;
  height: 13px;
  border-radius: 99px;
  background-color: #3b3b3b;
  border: 3px solid #868686;
  margin: auto 10px auto 5px;
}

#wrapper #drumMachine #controlWrap #trackPlayer .track .trackName {
  color: white;
  margin: 0;
}

#wrapper #drumMachine #controlWrap #padDisplay {
  text-align: center;
  width: 50%;
  display: float;
  float: right;
  margin-bottom: 40px;
}

#wrapper #drumMachine #controlWrap #padDisplay #padDisplayWindow {
  background-color: #80ffbd;
  margin: auto;
  padding: 15px;
  width: 120px;
  border: 3px solid #868686;
  border-radius: 5px;
  color: rgba(0, 0, 0, 0.3);
  font-family: "VT323", monospace;
  font-size: 30px;
  text-transform: uppercase;
}

#wrapper #drumMachine #controlWrap #volume {
  text-align: center;
  width: 50%;
  display: float;
  float: right;
}

#wrapper #drumMachine #controlWrap #volume #volumeSlider {
  -webkit-appearance: none;
  width: 80%;
  height: 6px;
  background: #181818;
  border-radius: 3px;
}

#wrapper #drumMachine #controlWrap #volume #volumeSlider::-moz-range-thumb {
  width: 12px;
  height: 28px;
  background: #b8b8b8;
  border: 3px solid #666666;
  border-radius: 3px;
}

#wrapper #drumMachine #controlWrap #volume #volumeSlider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 12px;
  height: 28px;
  background: #b8b8b8;
  border: 3px solid #666666;
  border-radius: 3px;
}

#wrapper #footer {
  color: #777777;
  text-align: center;
  margin: auto;
  bottom: 0%;
  position: fixed;
  width: 100%;
}

#wrapper #footer .footer-link {
  text-decoration: none;
  color: white;
}

@media (max-width: 900px) {
  #wrapper html {
    background: none;
    background-size: none;
    background-color: black;
  }
  #wrapper #footer {
    position: absolute;
    margin-left: 30px;
    bottom: 0%;
    top: 1000px;
  }
  #wrapper #drumMachine {
    width: 450px;
    height: 880px;
    display: inline-block;
    top: 490px;
  }
  #wrapper #drumMachine #padWrap {
    margin: 25px;
  }
  #wrapper #drumMachine #controlWrap {
    margin: 25px;
    height: 400px;
  }
}

@media (max-width: 500px) {
  #wrapper #footer {
    position: absolute;
    max-width: 400px;
    margin-left: 30px;
    bottom: 0%;
    top: 800px;
  }
  #wrapper #drumMachine {
    display: inline-block;
    position: absolute;
    top: 30px;
    left: 0;
    transform: translate(0, 0);
    border: none;
    border-radius: 0;
    background: rgba(0, 0, 0, 0);
    height: 800px;
  }
  #wrapper #drumMachine #padWrap {
    width: 300px;
    height: 300px;
    margin: 15px auto;
  }
  #wrapper #drumMachine #title {
    color: black;
  }
}
